import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Dog } from 'src/app/models/dog';
import { DogsService } from 'src/app/services/dogs.service';
import { environment } from 'src/environments/environment';

@Component({
	selector: 'app-dog-profile',
	templateUrl: './dog-profile.component.html',
	styleUrls: ['./dog-profile.component.css']
})
export class DogProfileComponent implements OnInit {
	dog:Dog;
	editBasic:boolean;
	form:FormGroup;
	environment = environment

	constructor(
		private dogsS:DogsService, 
		private route:ActivatedRoute,
		private fb:FormBuilder
	) {
		this.editBasic = false;
	}

	ngOnInit(): void {
		let id= this.route.snapshot.queryParamMap.get('id');

		this.form = this.fb.group({
			race:[''],
			gender:[''],
			size:[''],
			borndate:[''],
			description:[''],
			behaviour:[''],
			physic_act:['']
		})
		
		if(id)
			this.dogsS.getById(Number(id)).toPromise().then(dog => {
				this.dog = dog;

				this.form.patchValue({
					race: dog.race,
					gender: dog.gender,
					size: dog.size,
					borndate: dog.borndate,
					description: dog.description,
					behaviour: dog.behaviour,
					physic_act: dog.physic_act
				})
			})
	}

}
