import { Component, OnInit } from '@angular/core';
import { ModalService } from '../../../services/modal.service';
import { AlertsService } from '../../../services/alerts.service';
import { ServicesService } from 'src/app/services/services.service';
import { DogsService } from 'src/app/services/dogs.service';
import { UsersService } from 'src/app/services/users.service';

@Component({
	selector: 'app-active-services',
	templateUrl: './active-services.component.html',
	styleUrls: ['./active-services.component.css']
})
export class ActiveServicesComponent implements OnInit {
	dateManaged;
	walks:any[];
	dogs:any[];
	scoreButton;

	constructor(
		private modal:ModalService,
		private alert:AlertsService,
		private serviceS:ServicesService,
		private dogsS:DogsService,
		private usersS:UsersService
	) {
		this.walks = [];
		this.dogs = [];
	}

	ngOnInit(): void {
		let id = localStorage.getItem('id');

		this.dogsS.getByOwner(Number(id)).subscribe(res=>{
			this.dogs = res;

			this.serviceS.getApprovedById(Number(id)).subscribe(res1=>{
				this.walks = res1;

				this.walks.forEach(ww=>{
					this.serviceS.getDates(ww.id).subscribe(res2 => ww.dates = res2);

					this.serviceS.getDogs(ww.id).subscribe(res2=>{
						ww.dogs = [];
						res2.forEach(dId => ww.dogs.push(this.dogs.find(d => d.id == dId.dog_id)));
					});

					this.usersS.getById(ww.walker_id).subscribe(res2 =>{
						if(res2.id)
							ww.walker = res2;
					})
				})
			});
		})
	}

	chargeUser(id){
		let walker = this.walks.find(ww => ww.id == id).walker;
		
		let wd = [
			{name: 'Edad', value: this.getAge(walker.birthdate)},
			{name: 'Ocupacin', value: walker.ocupation},
			{name: 'Descripcin', value: walker.description}
		];

		setTimeout(()=>{
			this.modal.setModalData(
				"Paseador: " + walker.names + ' ' + walker.surname,
				this.modal.createVerticalTable(wd)
			);
		},10);
	}

	manageDate(id, wid){
		let w = this.walks.find(ww=>ww.id == wid);
		this.dateManaged= w.dates.find(dd=>dd.id == id);
		this.dateManaged.type = w.type;
		
		setTimeout(()=>{
			this.modal.setModalData(
				"Gestin WTD" + wid + "P" + id + ' ' + (this.dateManaged.type == 'WK' ? 'Paseo' : 'Cuidado'),
				document.getElementById("manageDate").innerHTML.split('id="').join('id="M')
			)

			if(this.dateManaged.collected == 0)
				this.modal.replicateChildren('cll', 'Mcll');
			else
				this.modal.replicateChildren('dlv', 'Mdlv');
		},10)
	}


	scoreDate(id, wid, score, comment){
		let w = this.walks.find(ww=>ww.id == wid);
		this.dateManaged = w.dates.find(dd=>dd.id == id);
		
		this.modal.setModalData(
			"Con cuanto calificaras este " + (w.type == 'WK' ? 'paseo': 'cuidado') + "?",
			`<div id="scoreG" class="form-group col-12">
				<div>
					<label>0</label><label class="float-right">5</label><br>
					<input id="scoreI" type="range" min="0" max="5" value="${score}" class="col-12">
				</div>
				<div col-12">
					<label>Comentario</label>
					<textarea id="scoreC" class="form-control" placeholder="Deja un comentario">${comment}</textarea>
				</div>
			</div>
			`
		);


		if(!this.scoreButton)
			this.scoreButton = document.getElementById('scoreButton').firstChild;
		
		document.getElementById('scoreG').appendChild(this.scoreButton);
	}

	getAge(date){
		let a = new Date(date);
		let b = new Date();
		a.setFullYear(b.getFullYear())

		return new Date().getFullYear() - new Date(date).getFullYear() + (a > b ? -1 : 0);
	}

	saveScore(){
		this.dateManaged.score = (document.getElementById('scoreI') as any).value;
		this.dateManaged.comment = (document.getElementById('scoreC') as any).value;
		this.dateManaged.status = 'end';
		
		this.serviceS.updateDate(this.dateManaged).subscribe(res => {
			if(res.ok)
				this.alert.showAlert("success","Calificacin ","Gracias por tu aporte",4)
			document.getElementById('modalButton').click();
		});
	}

	collectedDogs(){
		this.dateManaged.collected = 1;
		
		this.serviceS.updateDate(this.dateManaged).subscribe((res:any)=>{
			if(res.ok)
				this.alert.showAlert("success","Perritos recogidos ","Ya se llevan de paseo a tus perros.",4)

			this.modal.setModalData(
				null,
				document.getElementById("manageDate").innerHTML.split('id="').join('id="M')
			)

			this.modal.replicateChildren('dlv', 'Mdlv');
		});
	}

	deliveredDogs(){
		this.dateManaged.delivered = 1;
		
		this.serviceS.updateDate(this.dateManaged).subscribe(res => {
			if(res.ok)
				this.alert.showAlert("success","Perritos devueltos ","Regresaron tus perros.",4)

			this.modal.setModalData(
				null,
				document.getElementById("manageDate").innerHTML.split('id="').join('id="M')
			)
		});
	}
}
