import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from 'src/environments/environment';
import { Race } from '../models/race';

@Injectable({
	providedIn: 'root'
})
export class RacesService {
	private url = "races";

	constructor(public http:HttpClient) {}

    public insert(race:Race){
		return this.http.post<any>(environment.backendURL + this.url, race, environment.httpOptions)
	}

    public list(){
        return this.http.get<Race[]>(environment.backendURL + this.url);
    }
}
