import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { User } from 'src/app/models/user';
import { AlertsService } from 'src/app/services/alerts.service';
import { CitiesService } from 'src/app/services/cities.service';
import { UsersService } from 'src/app/services/users.service';

@Component({
  selector: 'app-request-walker',
  templateUrl: './request-walker.component.html',
  styleUrls: ['./request-walker.component.css']
})
export class RequestWalkerComponent implements OnInit {
	isSending:boolean;
	cities:any[];
	form:FormGroup;

  	constructor(
		public fb: FormBuilder, 
		private citiesS:CitiesService,
		private usersS:UsersService,
		private alert:AlertsService,
		private router:Router
  	) {
		  this.isSending = false;
	}

	ngOnInit(): void {
		this.citiesS.list().toPromise().then(res => this.cities = res)

		this.form = this.fb.group({
			names:['',Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(30),
				Validators.pattern("[A-Za-z]+")
			])],
			surname:['',Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(30),
				Validators.pattern("[A-Za-z]+")
			])],
			email:['',Validators.compose([
				Validators.required,
				Validators.email
			])],
			phone:['',Validators.compose([
				Validators.required,
				Validators.minLength(10),
				Validators.maxLength(10),
				Validators.pattern("[0-9]+")
			])],
			city_id:[1 ,Validators.compose([
				Validators.required
			])],
			address:['',Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(30)
			])],
			ocupation:['',Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(30)
			])],
			availability:['',Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(30)
			])],
			curriculum:[null, Validators.compose([
				Validators.required
			])],
			fileSource: [null],
			terms:[true,Validators.compose([
				Validators.required
			])],
		});
	}

	onSelectCurriculum(target) {
		const reader = new FileReader();

		if(target.files && target.files.length) {
			if(this.form.value.curriculum.endsWith(".doc") 
			|| this.form.value.curriculum.endsWith(".docx")
			|| this.form.value.curriculum.endsWith(".pdf")){
				const [file] = target.files;
				reader.readAsDataURL(file);
				reader.onload = () => {
					this.form.patchValue({
						fileSource: reader.result
					});
				};
			}else{
				this.form.patchValue({
					curriculum: null
				});
			}
		}
	}

	getFormValidationError(form:FormGroup){
		Object.keys(form.controls).forEach(key =>{
			const controlErrors:ValidationErrors = form.get(key).errors;
			let name = "";

			if(key == 'curriculum')
				name = "Hoja de vida";

			if(key == 'terms')
				name = "Terminos y condiciones";
			if(controlErrors != null){
				Object.keys(controlErrors).forEach(keyErr=>{
					let warn = "";

					if(keyErr == "required")
						warn = "Requerido: ";

					this.alert.showAlert("error", `${warn}`, `${name}`)
					
				})
			}
		});
	}



	trySend(){
		if(this.form.valid){
			this.isSending = true;
			var data:User = this.form.value;
			data.curriculum = data.curriculum.replace('C:\\fakepath\\','').split(' ').join('')
			data.req_walker = 1;
			this.alert.showAlert("warning", "Espera unos segundos por favor. ", "Enviando solicitud.")

			this.usersS.doRequestWalker(data).subscribe(res=>{
				console.log(res)
				if(res.ok){
					this.isSending = false;
					this.alert.showAlert("success", "Listo: ","Te contactarn pronto.")
					this.router.navigate(['/'])
				}else{
					this.alert.showAlert("error", "", res.text)
				}
					
			})
		}else{
			this.getFormValidationError(this.form)
		}
	}
}
