export const DAYS = [
  { id: 1, name: "Lunes" },
  { id: 2, name: "Martes" },
  { id: 3, name: "Mircoles" },
  { id: 4, name: "Jueves" },
  { id: 5, name: "Viernes" },
  { id: 6, name: "Sbado" },
  { id: 7, name: "Domingo" },
]

export function cloneDays(){
  let days = []
  DAYS.forEach(d => days.push(d))
  return days
}

export function getDay(id:number){
  return DAYS.find(d => d.id == id)
}