import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './pages/home/home.component';
import { PricingComponent } from './pages/pricing/pricing.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';

import { LoginComponent } from './pages/auth/login/login.component';
import { RegistrationComponent } from './pages/auth/registration/registration.component';

import { VerifyEmailComponent } from './pages/verify-email/verify-email.component';
import { ProfileComponent } from './pages/profile/profile.component';
import { DogProfileComponent } from './pages/dashboard/pets/dog-profile/dog-profile.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { PaymentComponent } from './pages/payment/payment.component';

import { StartComponent } from './pages/dashboard/start/start.component';
import { ActiveServicesComponent } from './pages/dashboard/active-services/active-services.component';
import { ServiceComponent } from './pages/dashboard/service/service.component';
import { AddPetComponent } from './pages/dashboard/pets/add-pet/add-pet.component';
import { ListPetsComponent } from './pages/dashboard/pets/list-pets/list-pets.component';
import { ClientHistoryComponent } from './pages/dashboard/client-history/client-history.component';
import { WalkerHistoryComponent } from './pages/dashboard/walker-history/walker-history.component';
import { WalkerServiceComponent } from './pages/dashboard/walker-service/walker-service.component';
import { ManageCitiesComponent } from './pages/dashboard/manage-cities/manage-cities.component';
import { ManageServicesComponent } from './pages/dashboard/manage-services/manage-services.component';
import { ManageWalkersComponent } from './pages/dashboard/manage-walkers/manage-walkers.component';
import { TrackingComponent } from './pages/dashboard/tracking/tracking.component';
import { BeWalkerComponent } from './pages/dashboard/be-walker/be-walker.component';
import { RequestWalkerComponent } from './pages/request-walker/request-walker.component';
//import { TestApiComponent } from './pages/test-api/test-api.component';
//import { } from '';

const routes: Routes = [
	{path:'', component:HomeComponent},
	{path:'pricing', component:PricingComponent},
	{path:'about-us', component:AboutUsComponent},
	{path:'login', component:LoginComponent},
	{path:'registration', component:RegistrationComponent},
	{path: 'request-walker', component:RequestWalkerComponent },
	{
		path:'dashboard', 
		component:DashboardComponent,
		children:[
			{path: '', component: StartComponent},
			{path: 'active-services', component: ActiveServicesComponent},
			{path: 'service', component: ServiceComponent},
			{path: 'add-pet', component: AddPetComponent},
			{path: 'list-pets', component: ListPetsComponent},
			{path: 'history', component: ClientHistoryComponent},
			{path: 'walker-history', component: WalkerHistoryComponent},
			{path: 'walker-service', component: WalkerServiceComponent},
			{path: 'manage-cities', component: ManageCitiesComponent},
			{path: 'manage-services', component: ManageServicesComponent},
			{path: 'manage-walkers', component: ManageWalkersComponent},
			{path: 'be-walker', component: BeWalkerComponent},
			{path: 'tracking', component: TrackingComponent}
		]
	},
	{path:'payment', component:PaymentComponent},
	{path:'verify-email', component:VerifyEmailComponent},
	{path:'profile', component:ProfileComponent},
	{path:'dog', component:DogProfileComponent},
	//{path:'test', component:TestApiComponent},
    { path: '**', redirectTo: 'not-found' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
