import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ScriptService } from '../../../services/script.service';
import { AlertsService } from '../../../services/alerts.service';
import { UsersService } from 'src/app/services/users.service';
import { DogsService } from 'src/app/services/dogs.service';
import { ServicesService } from 'src/app/services/services.service';
import { CitiesService } from 'src/app/services/cities.service';
import { Service } from 'src/app/models/service';
import { User } from 'src/app/models/user';
import { getDay } from 'src/app/models/constants';
import { MonthPlan } from 'src/app/models/models';

interface Res {
  ok?: boolean,
  response?
}

@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.css']
})
export class ServiceComponent implements OnInit {
  user: User;
  cService: Service;
  cities: any[];
  dogs: any[];
  selectedDogs: any[];
  selectedCity;
  selectedService;
  dates: any[];
  getDay = getDay

  showPreviewFunc:(boolean, any) => void

  monthPlan: MonthPlan

  @ViewChild('emailPreview')
  emailPreviewDiv:ElementRef<HTMLDivElement>

  @ViewChild('info')
  infoDiv:ElementRef<HTMLDivElement>

  @ViewChild('monthPlanC')
  monthPlanRef:ElementRef

  constructor(
    private script: ScriptService,
    private usersS: UsersService,
    private dogsS: DogsService,
    private citiesS: CitiesService,
    private servicesS: ServicesService,
    private alert: AlertsService
  ) {
    this.selectedDogs = [];
    this.dates = [];

    this.showPreviewFunc = this.showEmailPreview.bind(this)
  }

  ngOnInit(): void {
    this.usersS.getById(Number(localStorage.getItem('id'))).subscribe(user => {
      this.user = user;

      this.citiesS.list().toPromise().then(cities => {
        this.cities = cities;
        this.selectedCity = this.cities.find(c => c.id == this.user.city_id);

        this.dogsS.getByOwner(this.user.id).subscribe(dogs => {
          this.dogs = dogs;

          this.servicesS.getUncompleted(this.user.id).subscribe(service => {
            if (service) {
              this.cService = service;
              this.selectedCity = this.cities.find(c => c.id == this.cService.city_id);
              this.selectedService = this.cService.type;

              this.cService.dogs.forEach(d => {
                let dog = this.dogs.find(dog => dog.id == d.dog_id);
                let dogId = this.dogs.indexOf(dog);
                this.dogs.splice(dogId, 1);
                this.selectedDogs.push(dog);
              })

              this.cService.dates.forEach(d => {
                this.dates.push({ date: d.date, time: d.start_time, hours: d.hours, datetime: new Date(d.date + ' ' + d.start_time) });
              });

              if (this.cService.completed == 1) {
                this.showAbstract(true);
              } else {
                this.showInfo();
              }
            } else {
              console.log("No existe servicio previo")
              this.selectedService = 'WK';
              this.showInfo();
            }
          })
        });
      })
    })
  }

  putOnList() {
    let select = document.getElementById('dogList').getElementsByTagName('select')[0];
    let dog = this.dogs.find(dog => dog.id == select.value);
    let dogId = this.dogs.indexOf(dog);

    this.dogs.splice(dogId, 1);
    this.selectedDogs.push(dog);
    let cId = localStorage.getItem('id');

    this.servicesS.getUncompleted(Number(localStorage.getItem('id'))).subscribe(service => {
      if (service) {
        this.cService = service;
        this.servicesS.insertDog(this.cService.id, dog.id).subscribe(res1 => {
          if (res1.ok)
            this.alert.showAlert("success", dog.name + ": ", "Agregado a los paseos", 4);
          else
            this.alert.showAlert("error", "Error: ", "CD. No se pudo establecer conexin con la base de datos.", 4);
        })
      } else {
        this.cService = {
          completed: 0,
          approved: 0,
          actived: 0,
          finished: 0,
          inprogress: 0,
          city_id: this.selectedCity.id,
          client_id: cId,
          type: this.selectedService
        };
        this.servicesS.insert(this.cService).subscribe(resW => {
          this.cService.id = resW.id;

          this.servicesS.insertDog(resW.id, dog.id).subscribe(res2 => {
            if (res2.ok)
              this.alert.showAlert("success", dog.name + ": ", "Agregado a los paseos", 4);
            else
              this.alert.showAlert("error", "Error: ", "CD. No se pudo establecer conexin con la base de datos.", 4);
          })
        })
      }
    })
  }

  deleteOnList(id) {
    let dog = this.selectedDogs.find(dog => dog.id == id);
    let dogId = this.selectedDogs.indexOf(dog);
    this.selectedDogs.splice(dogId, 1);
    this.dogs.push(dog);

    this.servicesS.getUncompleted(Number(localStorage.getItem('id'))).subscribe(res => {
      this.servicesS.deleteDog(res.id, dog.id).subscribe(res1 => {
        if (res1.ok)
          this.alert.showAlert("success", dog.name + ": ", "Eliminado de los paseos", 4);
        else
          this.alert.showAlert("error", "Error: ", "DD. No se pudo establecer conexin con la base de datos.", 4);
      })
    })
  }

  addDate() {
    if (this.selectedService == 'WK')
      this.dates.push({ datetime: null, hours: 1 });
    else
      this.dates.push({ datetime: null, hours: 5 });
  }

  removeDate(i) {
    this.dates.splice(i, 1);
  }

  showAbstract(completed = false) {
    let abstractForm = document.getElementById('abstract');

    if (completed) {
      this.hideInfo();
      abstractForm.hidden = false;
    } else {
      let dateinputs = document.getElementsByClassName('form-datetime') as HTMLCollectionOf<HTMLInputElement>;

      for (var i = 0; i < this.dates.length; ++i) {
        this.dates[i].date = dateinputs[i * 3].value;
        this.dates[i].time = dateinputs[i * 3 + 1].value;
        this.dates[i].hours = dateinputs[i * 3 + 2].value;
        this.dates[i].datetime = new Date(dateinputs[i * 3].value + ' ' + dateinputs[i * 3 + 1].value);
      }

      this.servicesS.getUncompleted(Number(localStorage.getItem('id'))).subscribe(service => {
        console.log(service.id, this.dates)
        this.servicesS.insertDate(service.id, this.dates).subscribe(res1 => {
          console.log(res1)
          let w = service;
          w.city_id = this.selectedCity.id;
          w.type = this.selectedService;
          w.completed = 1;

          this.servicesS.update(w).subscribe(res2 => {
            if (res2.ok)
              this.alert.showAlert("success", "Servicio: ", "Guardado", 4);
            else
              this.alert.showDBErrorAlert("UW. ");

            this.hideInfo();
            abstractForm.hidden = false;
          });
        })
      })
    }
  }

  hideAbstract() {
    let abstractForm = document.getElementById('abstract');
    abstractForm.hidden = true;
  }

  showInfo() {
    let info = document.getElementById('info');
    info.hidden = false;
    this.hideAbstract();
    this.hidePayments();
  }

  hideInfo() {
    let info = document.getElementById('info');
    info.hidden = true;
  }

  getTotal() {
    let sum: number = 0;

    if (this.selectedCity) {
      this.dates.forEach(dat => {
        sum += Number(this.selectedCity.prices.find(p => p.type == this.selectedService && p.hours == dat.hours).value);
      })

      return this.selectedDogs.length * sum;
    }
    return 0;
  }

  getDescription() {
    let data = "";

    if (this.selectedCity)
      data += this.selectedCity.name + " Perros: " + this.selectedDogs.length;

    return data;
  }

  showPayments() {
    this.servicesS.getUncompleted(Number(localStorage.getItem('id'))).subscribe(service => {
      let w = service;
      w.total = this.getTotal();

      this.servicesS.update(w).subscribe(res2 => {
        if ((res2 as any).ok)
          this.alert.showAlert("success", "Servicio: ", "Guardado", 4);
        else
          this.alert.showDBErrorAlert("UWT. ");
      });
    })
    let btn = document.getElementById('epayco-sc');

    if (btn)
      btn.parentElement.removeChild(btn);

    let attrs = [
      { key: "id", value: "epayco-sc" },
      { key: "class", value: "epayco-button" },
      { key: 'data-epayco-key', value: '90b26c01ce528c00eee44b525299a92b' },
      { key: 'data-epayco-button', value: 'https://369969691f476073508a-60bf0867add971908d4f26a64519c2aa.ssl.cf5.rackcdn.com/btns/btn6.png' },
      { key: 'data-epayco-external', value: 'true' },
      { key: 'data-epayco-lang', value: 'es' },
      { key: 'data-epayco-amount', value: this.getTotal() },
      { key: 'data-epayco-tax', value: '0' },
      { key: 'data-epayco-tax-base', value: this.getTotal() },
      { key: 'data-epayco-name', value: 'WalkTheDog - Paseo Canino' },
      { key: 'data-epayco-description', value: this.getDescription() },
      { key: 'data-epayco-currency', value: 'cop' },
      { key: 'data-epayco-country', value: 'CO' },
      { key: 'data-epayco-test', value: 'true' },
      // {key:'data-epayco-invoice',value:'WTD'+this.walk.id },
      { key: 'data-epayco-response', value: 'http://localhost:4200/payment' },
      { key: 'data-epayco-confirmation', value: '' }
    ];
    this.script.loadScript('epayco', attrs).then(res => {
    })

    let info = document.getElementById('payment-methods');
    info.hidden = false;
  }

  showEmailPreview(isFailed:boolean, monthPlan:MonthPlan) {
    if (!isFailed) {
      this.monthPlan = monthPlan
      this.emailPreviewDiv.nativeElement.hidden = false
      this.infoDiv.nativeElement.hidden = true
    }
  }

  editEmailData(){
    this.emailPreviewDiv.nativeElement.hidden = true
    this.infoDiv.nativeElement.hidden = false
  }

  sendEmail(){
    this.servicesS.sendMonthPlanEmail({ user:this.user, monthPlan: this.monthPlan }).subscribe(res => {
      if(res.email){
        this.alert.showAlert("success", "Solicitud enviada", "La solicitud se envi correctamente")
        this.emailPreviewDiv.nativeElement.hidden = true
        this.infoDiv.nativeElement.hidden = false
      }else{
        this.alert.showAlert("error", "Hubo un error", "Hubo un error al enviar la solicitud, intenta de nuevo ms tarde")
      }
    }, err => console.log(err))
  }

  hidePayments() {
    let info = document.getElementById('payment-methods');

    if (info)
      info.hidden = true;
  }

  setCity(e) {
    if (this.selectedCity)
      this.selectedCity = this.cities.find(c => c.id == e.target.value);
  }

  setService(e) {
    if (this.selectedService) {
      this.selectedService = e.target.value;
      this.dates = [];
    }
  }

  showCityForm() {
    document.getElementById('cityForm').hidden = false;
    document.getElementById('data').hidden = true;
  }

  showServiceForm() {
    document.getElementById('serviceForm').hidden = false;
    document.getElementById('data').hidden = true;
  }

  showData() {
    document.getElementById('serviceForm').hidden = true;
    document.getElementById('cityForm').hidden = true;
    document.getElementById('data').hidden = false;
  }

  getCity(id) {
    return this.cities.find(c => c.id == id);
  }

  get getCities() {
    return this.cities ? this.cities : null;
  }

  getHoursPrice(hours) {
    return this.selectedCity.prices.find(p => p.hours == hours).value;
  }

  showNequi() {
    document.getElementById('nequi').hidden = false;
    document.getElementById('davi').hidden = true;
  }

  showDavi() {
    document.getElementById('davi').hidden = false;
    document.getElementById('nequi').hidden = true;
  }
}
