import { Injectable } from '@angular/core';
import { Dog } from '../models/dog';
import { HttpClient } from "@angular/common/http";
import { environment } from 'src/environments/environment';

@Injectable({
	providedIn: 'root'
})
export class DogsService {
	private url = "dogs";

	constructor(public http:HttpClient) {}

    public insert(dog:Dog){
		return this.http.post<any>(environment.backendURL + this.url, dog, environment.httpOptions)
	}

	public getByOwner(id:number){
		return this.http.get<Dog[]>(environment.backendURL + this.url + '/byowner/' + id)
	}

	public getById(id:number){
		return this.http.get<Dog>(environment.backendURL + this.url + '/' + id)
	}

	public helloPOST(){
		return this.http.post<any>(environment.backendURL + 'hello/world', { message: 'Hola mundo' }, environment.httpOptions)
	}
}
