import { Component, OnInit } from '@angular/core';
import { ServicesService } from 'src/app/services/services.service';

@Component({
	selector: 'app-walker-history',
	templateUrl: './walker-history.component.html',
	styleUrls: ['./walker-history.component.css']
})
export class WalkerHistoryComponent implements OnInit {
	walks:any[];
	constructor(private servicesS:ServicesService) { }

	ngOnInit(): void {
		let id = Number(localStorage.getItem('id'));

		this.servicesS.getFinishedByWalkerId(id).toPromise().then(resW=>{
			this.walks = resW;

			this.walks.forEach(ww=>{
				this.servicesS.getDates(ww.id).subscribe(res2=>{
					ww.dates = res2;
				});
			})
		});
	}

}
