import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from 'src/environments/environment';
import { City } from '../models/city';

@Injectable({
	providedIn: 'root'
})
export class CitiesService {
	private url = "cities";

	constructor(public http:HttpClient) {}

    public insert(city:City){
		return this.http.post<any>(environment.backendURL + this.url, city, environment.httpOptions)
	}

    public list(){
        return this.http.get<City[]>(environment.backendURL + this.url);
    }

	public updatePrices(prices){
		return this.http.post<any>(environment.backendURL + this.url + '/prices/update', prices, environment.httpOptions)
	}
}
