import { Injectable } from '@angular/core';
import { User } from '../models/user';
import { HttpClient } from "@angular/common/http";
import { environment } from 'src/environments/environment';

@Injectable({
	providedIn: 'root'
})
export class UsersService {
	private url = "users";

	constructor(public http:HttpClient) {}

    public insert(user){
		return this.http.post<any>(environment.backendURL + this.url, user, environment.httpOptions)
	}

	public update(user){
		return this.http.post<any>(environment.backendURL + this.url+'/update', user, environment.httpOptions)
	}

    public getById(id:number){
		return this.http.get<User>(environment.backendURL + this.url + '/' + id);
	}

    public getByGoogleID(google_token:string){
		return this.http.get<User>(environment.backendURL + this.url + '/bygoogleid/' + google_token);
	}

	public getWalkers(){
		console.log(environment.backendURL + this.url + '/walkers')
		return this.http.get<User[]>(environment.backendURL + this.url + '/walkers');
	}

	public getPostulatedWalkers(){
		return this.http.get<User[]>(environment.backendURL + this.url + '/walkers/postulated');
	}

	public doRequestWalker(data){
		return this.http.post<any>(environment.backendURL + this.url + '/walkers', data, environment.httpOptions)
	}
}
