import { Component, OnInit } from '@angular/core';
import { DogsService } from 'src/app/services/dogs.service';
import { ServicesService } from 'src/app/services/services.service';
import { UsersService } from 'src/app/services/users.service';
import { ModalService } from '../../../services/modal.service';

interface Res {
	ok?:boolean
}

@Component({
	selector: 'app-client-history',
	templateUrl: './client-history.component.html',
	styleUrls: ['./client-history.component.css']
})
export class ClientHistoryComponent implements OnInit {
	walks;
	dogs;
	constructor(
		private dogsS:DogsService,
		private usersS:UsersService, 
		private servicesS:ServicesService,
		private modal:ModalService
	) { }

	ngOnInit(): void {
		let id = Number(localStorage.getItem('id'));

		this.dogsS.getByOwner(id).toPromise().then(resD=>{
			this.dogs = resD;

			this.servicesS.getFinishedByClientId(id).toPromise().then(resW=>{
				this.walks = resW;

				this.walks.forEach(ww=>{
					this.servicesS.getDates(ww.id).subscribe(res2=>{
						ww.dates = res2;
					});

					this.servicesS.getDogs(ww.id).subscribe(res2=>{
						ww.dogs = [];

						res2.forEach(dId=>{
							ww.dogs.push(this.dogs.find(d => d.id == dId.dog_id));
						});
					});

					this.usersS.getById(ww.walker_id).subscribe(res2 =>{
						if(res2.id)
							ww.walker = res2;
					})
				})
			});
		})
	}

	chargeUser(id){
		let walker = this.walks.find(ww => ww.id == id).walker;
		
		let wd = [
			{name: 'Edad', value: this.getAge(walker.birthdate)},
			{name: 'Ocupacin', value: walker.ocupation},
			{name: 'Descripcin', value: walker.description}
		];

		setTimeout(()=>{
			this.modal.setModalData(
				"Paseador: " + walker.names + ' ' + walker.surname,
				this.modal.createVerticalTable(wd)
			);
		},10);
	}

	getAge(date){
		let a = new Date(date);
		let b = new Date();
		a.setFullYear(b.getFullYear())

		return new Date().getFullYear() - new Date(date).getFullYear() + (a > b ? -1 : 0);
	}

}
