import { Injectable } from '@angular/core';
import Swal from 'sweetalert2'
import { SweetAlertIcon } from 'sweetalert2';

@Injectable({
	providedIn: 'root'
})
export class AlertsService {
	container:HTMLElement;
	constructor() {
		this.container = document.getElementById('alert-container');
	}

	showAlert(context:SweetAlertIcon, title:string, content:string, seconds:number = 5){
		Swal.fire({
			icon: context,
			title,
			text:content,
			timer: seconds * 1000,
			timerProgressBar: true
		});
		/*let alert = this.createAlert(context, title, content);
		this.container.appendChild(alert);

		if(seconds){
			setTimeout(()=>{
				if(this.container.contains(alert))
					this.container.removeChild(alert);
			}, seconds * 1000);
		}*/
	}

	showDBErrorAlert(code = ""){
		let alert = this.createAlert("danger", "Error: ", code + "No se ha podido establecer conexin a la base de datos.");
		this.container.appendChild(alert);

		setTimeout(()=>{
			if(this.container.contains(alert))
				this.container.removeChild(alert);
		}, 4000);
	}

	private createAlert(context:string, title:string, content:string):HTMLElement{
		let alert = document.createElement('div');
		alert.classList.add('alert');
		alert.classList.add('alert-' + context);
		alert.classList.add('alert-dismissible');
		alert.classList.add('fade');
		alert.classList.add('show');
		alert.setAttribute("role","alert");

		let strong = document.createElement('strong');
		let span = document.createElement('span');
		strong.innerHTML = title;
		span.innerHTML = content;

		let close = document.createElement('button');
		close.classList.add('close');
		close.type = "button";
		close.setAttribute("data-dismiss", "alert")
		close.setAttribute("aria-label", "Cerrar");
		close.innerHTML ='<span aria-hidden="true">&times;</span>';

		alert.appendChild(strong);
		alert.appendChild(span);
		alert.appendChild(close);

		return alert;
	}
}
