import { Injectable, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { EventEmitter } from 'events';

@Injectable({
	providedIn: 'root'
})
export class WebSocketService {
	ready:boolean;
	socket:WebSocket;
	evEmitter:EventEmitter;

	constructor() {
		this.socket = new WebSocket(environment.serverSocket);
		this.evEmitter = new EventEmitter();

		this.socket.onopen = ()=>{
			this.ready = true;

			this.socket.onmessage = (e)=>{
				let data = JSON.parse(e.data);
				this.evEmitter.emit(data.event, data.data);
			}
		}

		this.socket.onclose = ()=>{
			this.ready = false;
			this.socket.onmessage = null;
		}
	}

	send(event:string, value:any){
		this.socket.send(JSON.stringify({event, value}))
	}

	receive(event:string, listener:(...args)=> void){
		this.evEmitter.on(event, listener);
	}

	get(entity:string, type:string, where?:string){
		if(this.ready)
			this.send('GET', {event: 'GET-'+ entity + '-' + type, entity, type, where});
		else{
			setTimeout(()=>{
				if(this.ready)
					this.send('GET', {event: 'GET-'+ entity + '-' + type, entity, type, where});
			}, 1000);
		}
		
		return new Observable(
			observer =>this.receive(
				'GET-'+ entity + '-' + type, 
				res => observer.next(res)
			)
		)
	}

	update(entity:string, object:any){
		this.send('UPDATE', {entity, object});
	}
}
