import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { OSM } from 'ol/source';
import { Tile as TileLayer } from 'ol/layer';
import { Overlay, View, Map } from 'ol';
import { fromLonLat } from 'ol/proj';
import { ServicesService } from 'src/app/services/services.service';

@Component({
	selector: 'app-tracking',
  	templateUrl: './tracking.component.html',
  	styleUrls: ['./tracking.component.css']
})
export class TrackingComponent implements OnInit, OnDestroy {
	isReading:boolean;
	markerEl;
	map:Map;
	view:View;
	marker:Overlay;
	geolocateBtn;
	tileLayer:TileLayer;
	interval;

  	constructor(
  		private servicesS:ServicesService, 
  		private route:ActivatedRoute
  	) { }

  	ngOnInit(): void {
  		this.isReading = false;

  		this.view = new View({
			center: fromLonLat([-73.356609, 5.5446507]),
			zoom: 19,
		});

		this.tileLayer = new TileLayer({
			source: new OSM(),
		});

		this.map = new Map({
			layers: [this.tileLayer],
			target: 'map',
			view: this.view,
		});

		this.markerEl = document.getElementById('geolocation_marker');

		this.marker = new Overlay({
			positioning: 'center-center',
			element: this.markerEl,
			stopEvent: false,
		});

		this.map.addOverlay(this.marker);

		this.geolocateBtn = document.getElementById('geolocate');
		this.geolocateBtn.hidden = false;

  		this.servicesS.getCurrentPosition(Number(this.route.snapshot.queryParamMap.get('id'))).toPromise().then(res=>{
			if(res.entity.latitude && res.entity.longitude){
				this.showPosition(fromLonLat([res.entity.longitude, res.entity.latitude]));
			}
		});
  	}

  	ngOnDestroy():void{
  		this.isReading = false;
  	}

	showPosition(pos) {
		this.view.setCenter(pos);
		this.marker.setPosition(pos);
		this.map.render();
  	}

	gBtn(){
		this.isReading = true;
		
		this.interval = setInterval(()=>{
			if(this.isReading){
				this.servicesS.getCurrentPosition(Number(this.route.snapshot.queryParamMap.get('id'))).toPromise().then(res=>{
					if(res.entity.latitude && res.entity.longitude){
						this.showPosition(fromLonLat([res.entity.longitude, res.entity.latitude]));
					}
				});
			}
		}, 1000)
	}

	sBtn(){
		this.isReading = false;
		clearInterval(this.interval);
	}
}
