import { Injectable } from '@angular/core';
import { Dog } from '../models/dog';
import { HttpClient } from "@angular/common/http";
import { environment } from 'src/environments/environment';
import { Service } from '../models/service';

@Injectable({
	providedIn: 'root'
})
export class ServicesService {
	private url = "services";

	constructor(public http:HttpClient) {}

    public insert(service:Service){
		return this.http.post<any>(environment.backendURL + this.url, service, environment.httpOptions)
	}

	public update(service:Service){
		return this.http.post<any>(environment.backendURL + this.url + '/update', service, environment.httpOptions)
	}

	public getUncompleted(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/uncompleted/' + id)
	}

	public getApprovedById(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/approved/' + id)
	}

	public getApproved(){
		return this.http.get<any>(environment.backendURL + this.url + '/approved')
	}

	public getUnapproved(){
		return this.http.get<any>(environment.backendURL + this.url + '/unapproved')
	}

	public getInProgress() {
		return this.http.get<any>(environment.backendURL + this.url + '/inprogress')
	}

	public getInProgressByWalkerId(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/inprogress/bywalkerid/' + id)
	}

	public getFinished(){
		return this.http.get<any>(environment.backendURL + this.url + '/finished')
	}

	public getFinishedByClientId(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/finished/byclientid/' + id)
	}

	public getFinishedByWalkerId(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/finished/bywalkerid/' + id)
	}

	public insertDog(sid, did){
		return this.http.post<any>(environment.backendURL + this.url + '/' + sid + '/dogs', { id:did }, environment.httpOptions);
	}

	public insertDate(id:number, dates:any[]){
		return this.http.post(environment.backendURL + this.url + '/' + id + '/dates', dates, environment.httpOptions)
	}

	public insertPosition(pos:any){
		return this.http.post(environment.backendURL + this.url + '/position', pos, environment.httpOptions)
	}

	public getCurrentPosition(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/position/' + id)
	}

	public deleteDog(sid, did){
		return this.http.get<any>(environment.backendURL + this.url + '/' + sid + '/dogs/' + did + '/delete');
	}

	public getDogs(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/' + id + '/dogs');
	}

	public getDates(id:number){
		return this.http.get<any>(environment.backendURL + this.url + '/' + id + '/dates');
	}

	public updateDate(date:any){
		return this.http.post<any>(environment.backendURL + this.url + '/dates/update', date, environment.httpOptions)
	}

	public uploadDateImage(image){
		return this.http.post<any>(environment.backendURL + this.url + '/image', image, environment.httpOptions)
	}

	public sendMonthPlanEmail(monthPlan) {
		return this.http.post<any>(environment.backendURL + this.url + '/monthplan', monthPlan, environment.httpOptions)
	}
}
