import { Injectable } from '@angular/core';

const myScripts = [
  { name: 'epayco', src: 'https://checkout.epayco.co/checkout.js', where:'epayco'}
];

@Injectable({
	providedIn: 'root'
})
export class ScriptService {
	private scripts: any = {};

	constructor() {
		myScripts.forEach((script: any) => {
			this.scripts[script.name] = {
				loaded: false,
				src: script.src,
				where:script.where
			};
		});
	}

	load(...scripts: string[]) {
		const promises: any[] = [];
		scripts.forEach((script) => promises.push(this.loadScript(script)));
		return Promise.all(promises);
	}

	loadScript(name: string, attrs?:any[]) {
		return new Promise((resolve, reject) => {
			if (this.scripts[name].loaded) {
				resolve({script: name, loaded: true, status: 'Already Loaded'});
			} else {
				const script = document.createElement('script');
				script.src = this.scripts[name].src;

        		if(attrs){
        			attrs.forEach(a =>{
        				script.setAttribute(a.key,a.value);
        			})
        		}

      			script.onload = () => {
        			this.scripts[name].loaded = true;
        			resolve({script: name, loaded: true, status: 'Loaded'});
      			};
        		
        		script.onerror = (error: any) => resolve({script: name, loaded: false, status: 'Loaded'});
        		
        		document.getElementById(this.scripts[name].where).appendChild(script);
        	}
        });
	}
}
