import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidationErrors } from "@angular/forms";
import { AlertsService } from '../../../services/alerts.service';
import { ServicesService } from 'src/app/services/services.service';
import { DogsService } from 'src/app/services/dogs.service';
import { environment } from 'src/environments/environment';

@Component({
	selector: 'app-walker-service',
	templateUrl: './walker-service.component.html',
	styleUrls: ['./walker-service.component.css']
})
export class WalkerServiceComponent implements OnInit {
	isTracking:boolean = false;
	walks:any[];
	dates:any[];
	todayDates:any[];
	activedDate;
	walkURL:string;
	formImage:FormGroup;
	watcher;

	constructor(
		public fb: FormBuilder,
		private alert:AlertsService,
		private servicesS:ServicesService,
		private dogsS:DogsService
	) {
		this.dates = [];
		this.todayDates = [];
	}
	
	ngOnInit(): void {
		this.formImage = this.fb.group({
			photo:[null,Validators.compose([
				Validators.required
			])],
			fileSource: ['', Validators.required],
			name:['']
		})
		this.servicesS.getInProgressByWalkerId(Number(localStorage.getItem('id'))).subscribe(res=>{
			this.walks = res;

			this.walks.forEach(ww=>{
				ww.dates.forEach(dd=>{
					dd.datetime = new Date(dd.date + " " + dd.start_time);
					dd.type = ww.type;
					this.dates.push(dd);
				});
			})

			this.dates.sort((a, b)=>{
				return a.datetime.getTime() - b.datetime.getTime();
			})

			let indexes:number[] = [];

			for (var i = 0; i < this.dates.length; ++i) {
				let dd = this.dates[i];

				if(this.isToday(dd.datetime)){
					this.todayDates.push(dd);
					indexes.push(dd.id);
				}
			}

			this.activedDate = this.todayDates.find(ddd => ddd.status == 'st');
			
			if(this.activedDate){
				this.servicesS.getDogs(this.activedDate.walk_id).subscribe(wDogs =>{
					this.activedDate.dogs = [];
					
					if(this.activedDate.photo)
						this.walkURL = environment.imagesURL + this.activedDate.photo;

					wDogs.forEach(d=>{
						this.dogsS.getById(d.dog_id).subscribe(dog=>{
							this.activedDate.dogs.push(dog);
						})
					});
				})
			}

			indexes.forEach(id =>{
				this.dates.splice(this.dates.indexOf(this.dates.find(dd=>dd.id==id)));
			})
		});
	}

	isToday(dd){
		const today = new Date()
		return dd.getDate() == today.getDate() 
			&& dd.getMonth() == today.getMonth() 
			&& dd.getFullYear() == today.getFullYear()

	}

	startDate(dateId){
		let date = this.todayDates.find(dd=> dd.id == dateId);

		date.status = 'st';

		this.servicesS.updateDate(date).subscribe(res=>{
			console.log(res)
			if(res.ok){
				this.activedDate = date;
				this.alert.showAlert("success","Paseo iniciado ","Procede a colocar la hora de tu llegada por los perritos.",4)

				this.servicesS.getDogs(this.activedDate.walk_id).subscribe(wDogs =>{
					this.activedDate.dogs = [];
					
					if(this.activedDate.photo)
						this.walkURL = environment.imagesURL + this.activedDate.photo;

					wDogs.forEach(d=>{
						this.dogsS.getById(d.dog_id).subscribe(dog=>{
							this.activedDate.dogs.push(dog);
						})
					});
				})
			}else{
				this.alert.showAlert("error","Error ","Hubo un error en el servidor.",4)
			}
		})
	}

	findMe() {
	    if (navigator.geolocation) {
	    	navigator.geolocation.getCurrentPosition((position) => {
	        	//this.showPosition(position);
	        	console.log(position);
	      	});
	    } else {
	      	alert("Tu navegador no soporta Geolocalizacn.");
	    }
	}

	trackMe(dateId) {
	    if (navigator.geolocation) {
	    	this.isTracking = true;
	    	this.watcher = navigator.geolocation.watchPosition((position) => {
	        	this.servicesS.insertPosition({
	    			id: dateId,
	    			latitude:position.coords.latitude,
	    			longitude:position.coords.longitude,
	    			tstamp:position.timestamp
	    		}).subscribe(res=>{
	        		console.log(res);
	        	});
	      	});
	    } else {
	      	alert("Tu navegador no soporta Geolocalizacn.");
	    }
	}

	stopTrack(){
		this.isTracking = false;
		navigator.geolocation.clearWatch(this.watcher);
	}

	saveStartTime(){
		this.activedDate.real_start_time = (document.getElementById('sWTime') as HTMLInputElement).value;
		
		this.servicesS.updateDate(this.activedDate).subscribe(res=>{
			if(res.ok)
				this.alert.showAlert("success","Hora de inicio ","Se ha registrado la hora de tu llegada.",4)
		});
	}

	saveFinalTime(){
		this.activedDate.real_final_time = (document.getElementById('fWTime') as HTMLInputElement).value;
		
		this.servicesS.updateDate(this.activedDate).subscribe(res=>{
			if(res.ok)
				this.alert.showAlert("success","Hora final ","Se ha registrado la hora de tu entrega.",4)
		});
	}

	saveDescription(){
		this.activedDate.description = (document.getElementById('WDescription') as HTMLInputElement).value;
		console.log(this.activedDate)
		this.servicesS.updateDate(this.activedDate).subscribe(res=>{
			console.log(res)
			if(res.ok)
				this.alert.showAlert("success","Descripcin ","Se ha registrado tu descripcin del paseo.",4)
		});
	}

	saveImages(){
		this.formImage.patchValue({
			name: 'photo' + this.activedDate.id + '-walk'
		});
		
		this.servicesS.uploadDateImage(this.formImage.value).subscribe(resI => {
			this.activedDate.photo = resI.name;
			
			this.servicesS.updateDate(this.activedDate).subscribe(res=>{
				if(res.ok)
					this.alert.showAlert("success","Foto ","Se ha guardado tu foto del paseo.",4)
			});
		})
	}

	onSelectWalk(event){
		const reader = new FileReader();

		if(event.target.files && event.target.files.length) {
			const [file] = event.target.files;
			reader.readAsDataURL(file);
			reader.onload = () => {
				this.walkURL = reader.result as string;
				this.formImage.patchValue({
					fileSource: reader.result
				});
			};
		}
	}

	finishDate(){
		if(this.activedDate.real_start_time 
			&& this.activedDate.real_final_time
			&& this.activedDate.description 
			&& this.activedDate.photo){

			this.activedDate.status = 'fh';

			this.servicesS.updateDate(this.activedDate).subscribe(res=>{
				if(res.ok)
					this.alert.showAlert("success","Final del paseo ","Gracias por cumplir tu deber.",4)
				this.activedDate = null;
			});
		}else{
			this.alert.showAlert("warning","Datos ","Revisa los campos, deben estar llenos todos.",4)
		}
		
	}

}
